.about-us-content {
  padding: 20px 50px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  .about-us-banner {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50%;
    @media (max-width: 767px) {
      min-width: 100%;
    }
  }

  .about-us-text{
    padding: 20px;
    width: 50%;
    min-width: 50%;
    p{
      text-align: justify;
    }
    @media (max-width: 767px) {
      padding: 20px 0;
      min-width: 100%;
    }
  }

  .about-us-img {
    border: 1px solid red;
    width: 50%;
    img {
      display: block;
      width: 100% !important;
      height: 200px !important;
      background-repeat: no-repeat;
      background-size: cover;
    }
  }
}


.about-us-clients{
  width: 90%;
  margin: auto;
  justify-content: center;
  //background: red;
  display: flex;
  flex-wrap: wrap;
  padding: 40px;
  div{
    margin: 10px;
  }
}
